<template><section>

<h2 id="tabs-biao-qian-ye"><a class="header-anchor" href="#tabs-biao-qian-ye" aria-hidden="true">¶</a> Tabs 标签页</h2>
<p>分隔内容上有关联但属于不同类别的数据集合。</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<p>基础的、简洁的标签页。</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-tabs v-model="activePanel">
    <fm-tab-panel :value="0" label="外汇">
        外汇
    </fm-tab-panel>
    <fm-tab-panel :value="1" label="贵金属">
        贵金属
    </fm-tab-panel>
    <fm-tab-panel :value="2" label="指数">
        指数
    </fm-tab-panel>
</fm-tabs>


</div>
                                                <p>Tabs 组件提供了选项卡功能，默认选中第一个标签页。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-tabs</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"activePanel"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"外汇"</span>&gt;</span>
        外汇
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"贵金属"</span>&gt;</span>
        贵金属
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"指数"</span>&gt;</span>
        指数
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">fm-tabs</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">activePanel</span>: <span class="hljs-number">0</span>
            }
        },

        <span class="hljs-attr">watch</span>: {
            activePanel (val) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'111111--panel'</span>, val);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<p>通过 <code v-pre="">value</code> 属性指定默认的选项：</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-tabs v-model="activePanel2" @chang="tabClick">
    <fm-tab-panel :value="0" label="外汇">
        外汇
    </fm-tab-panel>
    <fm-tab-panel :value="1" label="贵金属">
        贵金属
    </fm-tab-panel>
    <fm-tab-panel :value="2" label="指数">
        指数
    </fm-tab-panel>
</fm-tabs>


</div>
                                                <p><code>value</code> 属性指定默认的选项。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-tabs</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"activePanel2"</span> <span class="hljs-attr">@chang</span>=<span class="hljs-string">"tabClick"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"外汇"</span>&gt;</span>
        外汇
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"贵金属"</span>&gt;</span>
        贵金属
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"指数"</span>&gt;</span>
        指数
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">fm-tabs</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">activePanel2</span>: <span class="hljs-number">1</span>
            }
        },

        <span class="hljs-attr">methods</span>: {
            tabClick (val, e) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'点击了 tab '</span> + val);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="xuan-xiang-qia-yang-shi"><a class="header-anchor" href="#xuan-xiang-qia-yang-shi" aria-hidden="true">¶</a> 选项卡样式</h3>
<p>选项卡样式的标签页。</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-tabs type="card">
    <fm-tab-panel :value="0" label="外汇">
        外汇
    </fm-tab-panel>
    <fm-tab-panel :value="1" label="贵金属">
        贵金属
    </fm-tab-panel>
    <fm-tab-panel :value="2" label="指数">
        指数
    </fm-tab-panel>
</fm-tabs>
</div>
                                                <p>只需要设置 <code>type</code> 属性为 <code>card</code> 就可以使选项卡改变为标签风格。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-tabs</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"card"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"外汇"</span>&gt;</span>
        外汇
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"贵金属"</span>&gt;</span>
        贵金属
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"指数"</span>&gt;</span>
        指数
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">fm-tabs</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="dong-tai-zeng-jian-biao-qian-ye"><a class="header-anchor" href="#dong-tai-zeng-jian-biao-qian-ye" aria-hidden="true">¶</a> 动态增减标签页</h3>
<p>增减标签页按钮只能在选项卡样式的标签页下使用。</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><div class="btn-wrap">
    <fm-button @click="addTab"> 添加 tab </fm-button>
</div>
<fm-tabs type="card" v-model="activePanel3" closable="" @delete="deleteTab">
    <fm-tab-panel v-for="(tab, index) in tabs" :value="tab.value" :label="tab.label" :key="index">
        {{tab.content}}
    </fm-tab-panel>
</fm-tabs>


</div>
                                                <pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"btn-wrap"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"addTab"</span>&gt;</span> 添加 tab <span class="hljs-tag">&lt;/<span class="hljs-name">fm-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-tabs</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"card"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"activePanel3"</span> <span class="hljs-attr">closable</span> <span class="hljs-attr">@delete</span>=<span class="hljs-string">"deleteTab"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-tab-panel</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(tab, index) in tabs"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"tab.value"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"tab.label"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span>&gt;</span>
        {{tab.content}}
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-tab-panel</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">fm-tabs</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">activePanel3</span>: <span class="hljs-string">'贵金属'</span>,
                <span class="hljs-attr">tabs</span>: [
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'外汇'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'外汇'</span>,
                        <span class="hljs-attr">content</span>: <span class="hljs-string">'外汇1'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'贵金属'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'贵金属'</span>,
                        <span class="hljs-attr">content</span>: <span class="hljs-string">'贵金属1'</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'指数'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'指数'</span>,
                        <span class="hljs-attr">content</span>: <span class="hljs-string">'指数1'</span>
                    }
                ],
                <span class="hljs-attr">delIndex</span>: <span class="hljs-number">-1</span>
            }
        },
        <span class="hljs-attr">methods</span>: {
            deleteTab (value, e) {
                <span class="hljs-keyword">const</span> tabs = [].concat(<span class="hljs-keyword">this</span>.tabs);
                <span class="hljs-keyword">let</span> delIndex = <span class="hljs-number">-1</span>;

                <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; tabs.length; i++) {
                    <span class="hljs-keyword">if</span> (tabs[i].value === value) {
                        delIndex = i;
                        <span class="hljs-keyword">break</span>;
                    }
                }
                <span class="hljs-keyword">if</span> (delIndex &gt; <span class="hljs-number">-1</span>) {
                    <span class="hljs-keyword">this</span>.tabs.splice(delIndex, <span class="hljs-number">1</span>);
                    <span class="hljs-keyword">if</span> (value === <span class="hljs-keyword">this</span>.activePanel3) {
                        <span class="hljs-keyword">const</span> nextTab = tabs[delIndex + <span class="hljs-number">1</span>] || tabs[delIndex - <span class="hljs-number">1</span>];
                        <span class="hljs-keyword">this</span>.activePanel3 = nextTab.value;
                    }
                }
            },

            addTab () {
                <span class="hljs-keyword">const</span> len = <span class="hljs-keyword">this</span>.tabs.length;
                <span class="hljs-keyword">this</span>.tabs.push({
                    <span class="hljs-attr">value</span>: <span class="hljs-keyword">this</span>.tabs[len - <span class="hljs-number">1</span>] ? <span class="hljs-keyword">this</span>.tabs[len - <span class="hljs-number">1</span>].value + <span class="hljs-number">1</span> : <span class="hljs-string">'外汇0'</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">'外汇'</span> + (len + <span class="hljs-number">1</span>),
                    <span class="hljs-attr">content</span>: <span class="hljs-string">'add'</span> + (len + <span class="hljs-number">1</span>)
                });
                <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.tabs.length === <span class="hljs-number">1</span>) {
                    <span class="hljs-keyword">this</span>.activePanel3 = <span class="hljs-keyword">this</span>.tabs[<span class="hljs-number">0</span>].value;
                }
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="tabs-shu-xing"><a class="header-anchor" href="#tabs-shu-xing" aria-hidden="true">¶</a> Tabs 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">风格类型</td>
<td style="text-align:left">string</td>
<td style="text-align:left">card/panel</td>
<td style="text-align:left">panel</td>
</tr>
<tr>
<td style="text-align:left">closable</td>
<td style="text-align:left">标签是否可关闭</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定值，选中选项卡的 index</td>
<td style="text-align:left">string/number</td>
<td style="text-align:left">—</td>
<td style="text-align:left">第一个选项卡的 index</td>
</tr>
</tbody>
</table>
<h3 id="tabs-shi-jian"><a class="header-anchor" href="#tabs-shi-jian" aria-hidden="true">¶</a> Tabs 事件</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">事件名称</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">tab 选择发生变化时触发</td>
<td style="text-align:left">被选中的标签 tab 的 value</td>
</tr>
<tr>
<td style="text-align:left">delete</td>
<td style="text-align:left">点击 tab 移除按钮后触发</td>
<td style="text-align:left">被删除的标签的 value</td>
</tr>
</tbody>
</table>
<h3 id="tab-panel-shu-xing"><a class="header-anchor" href="#tab-panel-shu-xing" aria-hidden="true">¶</a> Tab-panel 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">唯一能标识 tab panel 的字段</td>
<td style="text-align:left">string/number</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">label</td>
<td style="text-align:left">选项卡标题</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>
<h3 id="slot"><a class="header-anchor" href="#slot" aria-hidden="true">¶</a> Slot</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">outer</td>
<td style="text-align:left">添加到 nav list 和 tab content 之间的部分</td>
</tr>
</tbody>
</table>
</section></template>
<style>
    .btn-wrap {
        margin-bottom: 15px;
    }
</style>
<script>
    export default {
        data () {
            return {
                activePanel: 0,
                activePanel2: 1,
                activePanel3: '外汇',
                tabs: [
                    {
                        value: '外汇',
                        label: '外汇',
                        content: '外汇1'
                    },
                    {
                        value: '贵金属',
                        label: '贵金属',
                        content: '贵金属1'
                    },
                    {
                        value: '指数',
                        label: '指数',
                        content: '指数1'
                    }
                ],
                delIndex: -1
            }
        },

        watch: {
            activePanel (val) {
                console.log('111111--panel', val);
            }
        },

        methods: {
            tabClick (val, e) {
                console.log('点击了 tab ' + val);
            },

            deleteTab (value, e) {
                const tabs = [].concat(this.tabs);
                let delIndex = -1;

                for (let i = 0; i < tabs.length; i++) {
                    if (tabs[i].value === value) {
                        delIndex = i;
                        break;
                    }
                }
                if (delIndex > -1) {
                    this.tabs.splice(delIndex, 1);
                    if (value === this.activePanel3) {
                        const nextTab = tabs[delIndex + 1] || tabs[delIndex - 1];
                        this.activePanel3 = nextTab.value;
                    }
                }
            },

            addTab () {
                const len = this.tabs.length;
                this.tabs.push({
                    value: this.tabs[len - 1] ? this.tabs[len - 1].value + 1 : '外汇0',
                    label: '外汇' + (len + 1),
                    content: 'add' + (len + 1)
                });
                if (this.tabs.length === 1) {
                    this.activePanel3 = this.tabs[0].value;
                }
            }
        }
    }
</script>


